<template>
  <Loading 
    :class="{
        'content--hide': !showLoading,
        'content--show': showLoading,
    }" 
    v-if="displayLoading"
  />
  <div
    :class="{
      'markdown-content': true,
      'content--hide': !showContent,
      'content--show': showContent,
    }"
    v-if="displayContent"
  >
    <div class="markdown-body" v-html="htmlContent"></div>
  </div>
</template>

<script>
import data from '!raw-loader!../assets/md/usage.md'
import Loading from '../components/Loading'
import { post } from "../utils/request";
import { ref, onMounted } from "vue";

let showLoading = ref(true);
let showContent = ref(false);
let displayLoading = ref(true);
let displayContent = ref(false);
const htmlContent = ref("");

export default {
  name: "Usage",
  components: { Loading },
  setup() {
    onMounted(async () => {
      if (showLoading.value) {
        const result = await post('markdown', { 
          "text": data,
          "mode": "markdown",
        })
        htmlContent.value = result;
        showLoading.value = false;
        setTimeout(() => {
          // 等待loading動畫結束
          displayLoading.value = false;
          displayContent.value = true;
          setTimeout(() => {
            // 等待 v-if 效果
            showContent.value = true;
          }, 100);
        }, 500);
      }
    });
    return {
      showLoading,
      showContent,
      displayLoading,
      displayContent,
      htmlContent,
    };
  },
};
</script>
<style lang="scss">
@import "../style/md.scss";
.content--hide {
  opacity: 0;
}

.content--show {
  opacity: 1;
}
</style>
